@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.container-one{
    min-height:100vh;
}
body,html{
    inset: 0;
    height: 100%;
    font-family:"Roboto" , sans-serif ;

  }
  
.contain-first{
  position: absolute;
  left: 0;
  top: 25%;
  width: 100%;
  text-align: center;
  color: #000;
  line-height: 7rem;

}
#info{
  color: #fff;
  text-shadow:
  0 0 92px rgb(224, 1, 57),
  0 0 97px rgb(0, 163, 109),
  0 0 110px rgba(0, 102, 255, 0.555);
}
.intro{
  font-size: 30px;
  color: #fff;
  text-shadow:
  0 0 8px rgb(2, 183, 255),
  0 0 20px #fff,
    0 0 20px rgb(0, 153, 255),
    0 0 30px rgb(0, 153, 255),
    0 0 92px rgb(224, 1, 57),
    0 0 97px rgb(0, 163, 109),
    0 0 110px rgba(0, 102, 255, 0.555);
  text-align: center;
  font-weight: 500;
  animation: glow 1s ease-in-out infinite alternate;
}
 .container-one {
    position:relative;
    opacity:0.83;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  

  .learn{
    padding: 1rem;
    margin: 16px;
  }
 
  .welcome{
    font-size: 50px;
    text-align: center;
    color: #fff;
    animation: glow 1s ease-in-out linear infinite ;
}

.parallax-one{
  position: relative;
  padding: 1rem;
  margin: 20px;
  color: rgba(0, 0, 0, 0.849);
}
.parallax-one h3{
  text-align: center;
  color: #000;
  letter-spacing:1px;
  font-weight: 550;
}
.parallax-one p{
  text-align: justify;
  overflow: hidden;
}

