 .container-two , .container-three{
    position:relative;
    opacity:0.83;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .contain-new{
        position: absolute;
        left: 0;
    top: 7%;
    width: 100%;
    text-align: center;
    color: #000;
  
}
 
