@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.container-two{

    min-height: 100vh;
  }
body,html{
    inset: 0;
    color: #777;
    height: 100%;
    font-family:"Roboto" , sans-serif ;

  }
 .container-two , .container-three{
    position:relative;
    opacity:0.83;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  h3 {
    letter-spacing: 5px;
    text-transform: uppercase;
   color: #000;
    text-align:center;
    text-align: justify;
  }
  .contain-form{
        position: absolute;
        left: 0;
    top: 18%;
    width: 100%;
    text-align: center;

  
}

  .contain-new span.topic{
    background-color: #111;
    color: #fff;
    padding: 18px;
    font-size: 25px;
    letter-spacing: 10px;
  }
.parallax-two, .parallax-three{
    position: relative;
  }
  #second-one h3{
    color: #fff;
    text-align: center;
  }
  #second-one{
    background-color: rgba(0, 0, 0, 0.89);  
    color: #fff;
     -webkit-backdrop-filter: blur(21px);
      backdrop-filter: blur(21px);
      box-shadow: 0 8px 32px 0 rgba(0,0,0,0.36);
      border: 1px solid rgba(43, 41, 41, 0.16);
      
  }
  
.footer {
	max-width: 70%;
	margin: 1rem auto;
	overflow: auto;
}

.footer,
.footer a {
	color: #999;
	font-size: 0.9rem;
}

.footer p {
	margin-bottom: 1.5rem;
}

.footer .footer-cols {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 2rem;
}

.footer li {
	line-height: 1.9;
}
.footer a:hover{
	color: #fff;
}
.footer .lang-select {
	margin-top: 2rem;
	color: #999;
	background-color: #000;
	background-image: none;
	border: 1px solid #333;
	padding: 1rem 1.2rem;
	border-radius: 5px;
}
.footer::-webkit-scrollbar {
  display: none;
}
.example {
  -ms-overflow-style: none;  
  scrollbar-width: none;  
}
#key{
  float: inline-end;
  text-align: right;
  margin-bottom: 2vh;
  color:rgba(255, 255, 255, 0.664);
  margin-right: 0;
}

.social-icons a {
  background-color: none;
  color: #fff;
  text-decoration: none;

  height: 20px;
  width: 20px;
  padding: 9px 12px;
  border-spacing: 2rem;
  border-radius: 50%;
  line-height: 14px;
  margin: 8px;
  border: 1px solid #fff; 

  
}
.social-icons a:hover {
  color: #000;
  background-color: #fff;
}
.social-icons #jone:hover{
  color: #fff;
  background-color: #0157d8;

}
.social-icons #jony:hover{
  color: #fff;
  background-image: linear-gradient(aqua,#106fff);
  
}