@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.container-three{
    min-height: 100vh;
}
.container-two{

    min-height: 500px;
  }
body,html{
    inset: 0;
    color: #777;
    height: 100%;
    font-family:"Roboto" , sans-serif ;

  }
 .container-two , .container-three{
    position:relative;
    opacity:0.83;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .container-three #hd{
    color: #fff;
    text-align: center;
    margin-top: 10%;
  }
  h3 {
    letter-spacing: 5px;
   color: #000;
    text-align:center;
    text-align: justify;
  }

  .contain-cards{
        position: absolute;
        left:0;
        padding: 5px;
        top: 20%;
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: #000;
  
}
#hd{
  letter-spacing: 2px;
  width: 200px;
  height: 50px;
  padding: 5px;
  border: 2px inset rgba(0, 0, 0, 0.63);
  background-color: rgba(0, 0, 0, 0.87);
}
#hd h2{
  text-align: center;
 
}
.parallax-two, .parallax-three{
    position: relative;
  }
  .cards{
    gap: 15px;
    display: flex;
    overflow-x: auto;
    position: relative;
  }
  .cards a{
    text-decoration: none;
    color: #0066ff;
    font-size: 22px;
  
  }
  .cards::-webkit-scrollbar {
		display: none;
	}
	.example {
	  -ms-overflow-style: none;  
	  scrollbar-width: none;  
  }
  #second h2{
    color: #000;
    text-align: center;
    letter-spacing: 1px;
    padding: 1.5rem;
  }
 