@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.container-three{
    min-height: 100vh;
}
.container{

    min-height: 500px;
  }
body,html{
    inset: 0;

    height: 100%;
    font-family:"Roboto" , sans-serif ;

  }
 .container, .container-three{
    position:relative;
    opacity:0.83;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  h3 {
    letter-spacing: 5px;
   color: #000;
    text-align:center;
    text-align: justify;
  }
  .contain{
        position: absolute;
        left: 0;
        top: 20%;
        width: 100%;
        text-align: center;
        color: #000;
      
  }

  .contain-two{
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    text-align: center;
    color: #000;
  
}
  .contain span.topic{
    background-color: #111;
    color: #fff;
    padding: 18px;
    font-size: 25px;
    letter-spacing: 10px;
  }
.parallax-two, .parallax-three{
    position: relative;
  }
  body a {
    background-color: none;
    color: #fff;
    text-decoration: none;
 }
  .card {
    position: absolute;
    top: 60%;
    left: 50%;
    height: 400px;
    width: 300px;
    transform: translate(-50%, -50%);
    transform-style: preserve-3d;
    perspective: 600px;
    transition: 0.5s;
  }
  .card:hover .card-front {
    transform: rotateX(0deg);
  }
  .card:hover .card-back {
    transform: rotateX(0deg);
  }
  
  .card-front {
    height: 100%;
    width: 100%;
    background-position: 50% 50%;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    transform: rotateX(0deg);
    transition: 0.5s; 
  }
  
  .card-back {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color:#000;
    color: #fff;
    -webkit-backface-visibility: hidden; 
    backface-visibility: hidden;
    transform: rotatey(180deg);
    transition: 0.5s;
    text-align: center;
    
  }
  .card-back h2 {
    margin: 42% auto 30% auto;
    font-size: 26px;
    line-height: 40px;
    
  }
  .card-back h2 span {
    font-size: 20px;
    background: linear-gradient(270deg,#b527cf,#0066ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
   
  }
 #next{
  background: linear-gradient(270deg,#0066ff,#b527cf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
  .card-back a {
    height: 20px;
    width: 20px;
    padding: 9px 12px;
    border-spacing: 1rem;
    border-radius: 50%;
    line-height: 9px;
    margin: 8px;
    border: 1px solid #fff; 
  }
  .card-back a:hover {
    color: #000;
    background-color: #fff;
  }
  .card-back #jone:hover{
    color: #fff;
    background-color: #0157d8;
    border: none;
  }
  .card-back #jony:hover{
    color: #fff;
    background-image: linear-gradient(aqua,#106fff);
    border: none;
  }